.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f0f0f0;
  box-sizing: border-box;
  width: 100%;
  max-width: 100vw;
  overflow-x: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-bottom: 30px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.chessboard-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 8px;
  overflow: hidden;
  width: 100%;
  max-width: 400px;
  position: relative;
}

.chessboard-container > div {
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: hidden;
}

.jogador-nome {
  font-size: 0.85em;
  padding: 3px 0;
  color: #666;
  text-align: left;
  margin: 2px 0;
  opacity: 0.8;
  margin-left: 15px;
}

.jogador-nome.brancas {
  color: #666;
}

.jogador-nome.pretas {
  color: #666;
}

.controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
  padding: 8px;
  padding-top: 0;
  max-width: 400px;
  margin: 0 auto;
}

.feedback-container {
  flex: 1;
  min-width: 0;
}

.feedback {
  font-size: 14px;
  padding: 10px 8px;
  border-radius: 4px;
  margin: 0;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 0;
  box-sizing: border-box;
  line-height: normal;
}

.feedback p {
  display: flex;
    align-items: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 1;
    text-align: center;
    width: 100%;
    font-size: 14px;
    height: 40px;
    font-weight: normal;
    justify-content: center;
}

.feedback.error {
  background-color: rgba(255, 0, 0, 0.1);
  color: #ff0000;
}

.feedback.success {
  background-color: #e6fbe6;
  color: #176c1a;
  font-weight: 600;
}

.feedback.info {
  background-color: rgba(0, 0, 0, 0.05);
  color: #333;
  margin: 0;
  padding: 0 8px;
}

.buttons-container {
  display: flex;
  gap: 8px;
  align-items: center;
}

.control-button {
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 50%;
  background-color: #f0f0f0;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  padding: 0;
  border: 1.5px solid #e0e0e0;
  box-shadow: 0 1px 4px rgba(0,0,0,0.04);
  margin: 0 4px;
}

.control-button:hover:not(:disabled) {
  background-color: #e0e0e0;
  transform: scale(1.05);
  border-color: #bdbdbd;
}

.control-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.control-button.error-button {
  background-color: #ffeaea;
  color: #c62828;
  border: 1.5px solid #ffbdbd;
  font-weight: 600;
}

.control-button.error-button:hover:not(:disabled) {
  background-color: #ffd6d6;
  border-color: #ff8a8a;
}

.control-button.error-button svg {
  color: #c62828;
}

.control-button svg {
  width: 24px;
  height: 24px;
}

.info {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 10px;

}

.info p {
  margin: 0;
  font-size: 1.1em;
  color: #333;
}

.info .tema {
  font-weight: bold;
  font-size: 1.2em;
}

.info .exercicio {
  color: #666;
  font-style: italic;
  margin: 5px 0;
}

.feedback-success {
  color: #2e7d32;
  font-weight: bold;
}

.feedback-error {
  color: #c62828;
  font-weight: bold;
}

.try-again {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
  font-size: 14px;
  transition: background-color 0.2s;
  min-width: 120px;
}

.try-again:hover {
  background-color: #d32f2f;
}



/* Dropdowns de filtro */
.dropdowns-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 400px;
  max-width: 100vw;
  margin: 0 auto 12px auto;
  gap: 10px;
  margin-top: 0.6rem;
}

.dropdown-wrapper {
  position: relative;
  width: 100%;
}

.dropdown-tema, .dropdown-nivel {
  width: 100%;
  max-width: 100vw;
  padding: 8px 48px 8px 14px;
  border-radius: 4px;
  border: 1px solid hsl(0, 0%, 73%, 0.7);
  background: #ffffff;
  font-size: 1em;
  font-family: 'Segoe UI', 'Roboto', Arial, sans-serif;
  color: #333;
  box-shadow: 0 1px 4px rgba(0,0,0,0.03);
  outline: none;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-image: none;
}

.dropdown-chevron {
  position: absolute;
  right: 18px;
  top: 50%;
  width: 24px;
  height: 24px;
  pointer-events: none;
  transform: translateY(-50%);
  background-image: url('./icons/chevron-down.svg');
  background-repeat: no-repeat;
  background-size: 20px 20px;
  background-position: center;
  display: block;
  right: 10px;
  width: 22px;
  height: 22px;
  background-size: 22px 22px;
  
}

.dropdown-tema:hover, .dropdown-nivel:hover {
  cursor: pointer;
}

/* Bot√£o de sucesso (continuar) */
.control-button.success-button {
  background-color: #e6fbe6;
  color: #176c1a;
  border: 1.5px solid #b6e2b6;
  font-weight: 600;
}
.control-button.success-button:hover:not(:disabled) {
  background-color: #c8f7c8;
  border-color: #7ed87e;
}
.control-button.success-button svg {
  color: #176c1a;
}

.control-button.unified-feedback-button {
  min-width: 300px;
  font-size: 1.08em;
  padding: 0 18px;
  border-radius: 12px;
  height: 44px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.control-button.unified-feedback-button .feedback-icon {
  width: 22px;
  height: 22px;
  margin-left: 10px;
}

.control-button.unified-feedback-button svg,
.control-button.unified-feedback-button .refresh-icon {
  display: inline-block;
  vertical-align: middle;
  align-self: center;
}

.control-button.unified-feedback-button.error-button {
  background-color: #ffeaea;
  color: #c62828;
  border: 1.5px solid #ffbdbd;
}
.control-button.unified-feedback-button.error-button:hover:not(:disabled) {
  background-color: #ffd6d6;
  border-color: #ff8a8a;
}

.control-button.unified-feedback-button.success-button {
  background-color: #e6fbe6;
  color: #176c1a;
  border: 1.5px solid #b6e2b6;
}
.control-button.unified-feedback-button.success-button:hover:not(:disabled) {
  background-color: #c8f7c8;
  border-color: #7ed87e;
}

.control-button.unified-feedback-button.success-button svg {
  color: #176c1a;
}

